import React from "react";
import {NavLink} from "react-router-dom";
function Navbar() {
    return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light">
  <div className="container-fluid">

    <NavLink className="navbar-brand font-weight-bold" to="/">MuscleTech</NavLink>
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
      <div className="navbar-nav ml-auto">
        <NavLink exact activeClassName="active-nav" className="nav-link " to="/">Home</NavLink>
        <NavLink exact activeClassName="active-nav" className="nav-link" to="/services">Services</NavLink>
        <NavLink exact activeClassName="active-nav" className="nav-link" to="/pricing">Pricing</NavLink>
        <NavLink exact activeClassName="active-nav" className="nav-link " to="/contact">Contact us</NavLink>
      </div>
    </div>
  </div>
</nav>

    </>
    
    
    );
};

export default Navbar;